var regularDelivery = {
    name: "John",

    handleMessage: function (msg, handler) {
        handler(msg);
    },

    receiveMessage: function () {

        this.handleMessage("Hello ", function (message) {
            console.log(message + this.name);
        })
    }
}

regularDelivery.receiveMessage();